:root {
    --primary: #3acbf7;
  }
  
  .btn {
    margin: 0 5px;
    padding: 8px 15px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
  }
  
  .btn:hover {
    padding: 8px 15px;
    transition: 0.3s ease-out;
    background: #fff;
    color: #6568f4;
    transition: 250ms;
  }
  
  .btn--primary {
    background-color: var(--primary);
  }
  
  .btn-outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 15px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  
  .btn--medium {
    padding: 8px 15px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
  }
  
  .btn--large {
    padding: 12px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
  }
  